<template>
<el-tabs type="border-card" class="center">
    <el-tab-pane label="">
        <template v-if="this.books.length">
            <BookTable :bookList="books" :bookTypes="bookTypes" @handleDelete="onDelete" @handleChange="onChange"/>
        </template>
        <template v-else>
             
        </template>
    </el-tab-pane>
    <el-tab-pane label="">
        <BookForm :bookTypes="bookTypes" @handleSubmit="onAdd" class="formPadding"/>
    </el-tab-pane>
</el-tabs>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import BookTable from "@/components/Book/BookTable";
import BookForm from "@/components/Book/BookForm";

export default {
  name: "BookDisplayer",
  components: { BookForm, BookTable },
  computed: {
    ...mapGetters('books', ['books']),
    ...mapGetters('bookTypes', ['bookTypes'])
  },
  methods: {
    ...mapActions('books', ['addBook', 'putBook', 'deleteBook']),
    onDelete(id) {
      this.$confirm('   . ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          await this.deleteBook(id);

          this.$message({
            type: 'success',
            message: '  '
          });
        } catch (e) {
          this.$message({
            type: 'error',
            message: '  '
          })
        }
      }).catch(() => {
      });
    },
    async onAdd(book) {
      try {
        await this.addBook({ ...book });
        this.$message({
          type: 'success',
          message: '  '
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: '  '
        })
      }
    },
    async onChange(book) {
      try {

        await this.putBook({ id: book.id, book });
        this.$message({
          type: 'success',
          message: '   '
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: '  '
        })
      }
    }
  }
}
</script>

