<template>
    <el-tabs type="border-card" class="center">
    <el-tab-pane label="">
        <template v-if="this.clients.length">
            <ClientTable :clientList="clients" @handleDelete="onDelete" @handleChange="onChange"/>
        </template>
        <template v-else>
             
        </template>
    </el-tab-pane>
    <el-tab-pane label="">
        <ClientForm @handleSubmit="onAdd" class="formPadding"/>
    </el-tab-pane>
</el-tabs>
</template>

<script>
import BookTypeForm from "@/components/BookType/BookTypeForm";
import BookTypeTable from "@/components/BookType/BookTypeTable";
import { mapActions, mapGetters } from "vuex";
import ClientTable from "@/components/Clients/ClientTable";
import ClientForm from "@/components/Clients/ClientForm";

export default {
  name: "ClientDisplayer",
  components: { ClientForm, ClientTable, BookTypeForm, BookTypeTable },
  computed: {
    ...mapGetters('clients', ['clients'])
  },
  methods: {
    ...mapActions('clients', ['addClient', 'putClient', 'deleteClient']),
    ...mapActions('journals', ['fetchJournals']),
    onDelete(id) {
      this.$confirm('   . ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          await this.deleteClient(id);
          this.$message({
            type: 'success',
            message: '  '
          });
        } catch (e) {
          this.$message({
            type: 'error',
            message: '  '
          })
        }
      }).catch(() => {
      });
    },
    async onAdd(client) {
      try {
        await this.addClient(client);
        this.$message({
          type: 'success',
          message: '  '
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: '  '
        })
      }
    },
    async onChange(client) {
      try {
        await this.putClient({ id: client.id, client });
        await this.fetchJournals();
        this.$message({
          type: 'success',
          message: '  '
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: '  '
        })
      }
    }
  }
}
</script>

